import { FontAwesomeIcon } from '../../../Assets/AssetsHelper';
import { isWebsiteChanged } from '../../../utils/checkForNavigation';

const MenuTextIcon = ({ icon, text }) => <>
    <span className="menu-icon me-2 me-lg-0 mb-0 mb-lg-2">
        <FontAwesomeIcon icon={icon} />
    </span>
    <span className="menu-text">{text}</span>
</>

const AsideMenu = ({ authorized, navigate }) => {
    const authData = authorized?.userData?.user_data?.sessionData
    const websiteUrl = authData?.createFrom.toLowerCase() == 'shopify' ? `shopify` : authData?.createFrom.toLowerCase() == 'bigcommerce' ? `bigcommerce` : 'publish'
    const navItemClass = `nav-menu-item f-center flex-lg-column text-decoration-none`
    return <div className="sidebar-menu">
        <ul className="sidebar-menu-list list-unstyled mb-0 p-0">
            <li className="sidebar-menu-items">
                <div className={`${navItemClass} ${window.location.pathname.includes('/home') ? 'active' : ''}`} onClick={() => isWebsiteChanged(() => navigate('/home'))}>
                    <MenuTextIcon icon="home" text="Home" />
                </div>
            </li>
            <li className="sidebar-menu-items">
                <div className={`${navItemClass} ${window.location.pathname.includes('/managefeed') || window.location.pathname.includes('/addfeed') ? 'active' : ''}`} onClick={() => isWebsiteChanged(() => navigate(`/content/managefeed/${localStorage.getItem('wallId') ? localStorage.getItem('wallId') : ''}`))}>
                    <MenuTextIcon icon="grid-2-plus" text="Feeds" />
                </div>
            </li>
            <li className="sidebar-menu-items">
                <div className={`${navItemClass} ${window.location.pathname.includes('/moderation') ? 'active' : ''}`} onClick={() => isWebsiteChanged(() => navigate(`/content/moderation/${localStorage.getItem('wallId') ? localStorage.getItem('wallId') : ''}`))}>
                    <MenuTextIcon icon="image" text="Content" />
                </div>
            </li>
            <li className="sidebar-menu-items">
                <div className={`${navItemClass} ${window.location.pathname.includes('/products') ? 'active' : ''}`} onClick={() => isWebsiteChanged(() => navigate(`/content/products`))}>
                    <MenuTextIcon icon="bag-shopping" text="Product Catalog" />
                </div>
            </li>
            <li className="sidebar-menu-items">
                <div className={`${navItemClass} ${/\/(website|email-campaign|shopon-bio)/.test(window.location.pathname) ? 'active' : ''}`} onClick={() => isWebsiteChanged(() => navigate(`/website/${websiteUrl}${localStorage.getItem('website_id') ? `/${localStorage.getItem('website_id')}` : ''}`))}>
                    <MenuTextIcon icon="paper-plane" text="Publish" />
                </div>
            </li>
            <li className="sidebar-menu-items">
                <div className={`${navItemClass} ${window.location.pathname.includes('/more-apps') ? 'active' : ''}`} onClick={() => isWebsiteChanged(() => navigate(`/more-apps/${localStorage.getItem('wallId') ? localStorage.getItem('wallId') : ''}`))}>
                    <MenuTextIcon icon="grid-2" text="Apps & More" />
                </div>
            </li>
            <hr className="w-75 d-lg-flex d-none text-gray-200" style={{ margin: '1vh auto' }} />
            <li className="sidebar-menu-items">
                <div className={`${navItemClass} ${window.location.pathname.includes('/creators') ? 'active' : ''}`} onClick={() => isWebsiteChanged(() => navigate(`/creators`))}>
                    <MenuTextIcon icon="user-group" text="Creators" />
                </div>
            </li>
            <li className="sidebar-menu-items">
                <div className={`${navItemClass} ${window.location.pathname.includes('/analytics') ? 'active' : ''}`} onClick={() => isWebsiteChanged(() => navigate(`/analytics#overview`))}>
                    <MenuTextIcon icon="chart-mixed" text="Analytics" />
                </div>
            </li>
        </ul>
    </div>
}
export default AsideMenu;